import React, { Component } from 'react'
import {Link } from 'react-router-dom'

export default class Homepage extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
             userName:'',
             userPassword:'',
             vailedName:false,
             vailedPass:false,
             user:props.user,
        }
    }
    vailedUserName = (element) => {
        if(element.target.value.length >= 4){
            this.setState({userName:element.target.value , vailedName:true});
        }else{
            this.setState({userName:element.target.value,vailedName:false});
        }
    }

    vailedUserPassword = (element) => {
        if(element.target.value.length >= 6 || element.target.value === 'ADMIN'){
            this.setState({userPassword:element.target.value , vailedPass:true});
        }else{
            this.setState({userPassword:element.target.value,vailedPass:false});
        }    
    }
    
    render() {
        let path = `/user/${this.state.userName}`
        let submit = <Link to = '/'><button className='btn btn-success' type="button" style={{width:'250px',height:'60px',fontSize:'30px'}}>LogIn</button></Link>;
        let error = 'Invalid username or password'
        if(this.state.vailedName === true && this.state.vailedPass === true){
            for(let i = 0;i<this.state.user.length;i++){
                if(this.state.user[i].name === this.state.userName && this.state.user[i].password === this.state.userPassword){
                    submit = <Link to = {path}><button className='btn btn-success' type="button" style={{width:'250px',height:'60px',fontSize:'30px'}}>LogIn</button></Link>
                    error = ''
                }
            }       
        }
        return (
            <div className='App'>
                <div style={{backgroundColor:'#2093c3'}}>
                    <p style={{fontSize:'60px',fontFamily:'Times new roman',color:'white'}}> SV-BANK </p>
                </div>
                <br/>
                <br/>
                <input onChange={this.vailedUserName} placeholder='User Name' type='text' style={{width:'300px',height:'45px',fontSize:'30px'}}></input>
                <br/>
                <br/>
                <input onChange={this.vailedUserPassword} placeholder='Password' type='password' style={{width:'300px',height:'45px',fontSize:'30px'}}></input>
                <br/>
                <br/>   
                <p style={{color:'red',fontSize:'20px'}}> {error} </p>
                <Link to='/register'> <button type='button' className='btn btn-link' style={{width:'250px',height:'60px',fontSize:'30px'}}>Create new user </button> </Link>
                <br/>
                <br/>
               {submit}
            </div>
        )
    }
}