import React, { Component } from 'react'
import { BrowserRouter as Router , Route , Switch} from 'react-router-dom'
import './App.css';
import 'bootstrap/dist/css/bootstrap.css'
import Homepage from './component/Homepage';
import Register from './component/Register';
import Manager from './component/Manager';
import User from './component/User';
import Edit from './component/Edit';

export default class App extends Component {
  
  state = {
    user:[],
  }
  
  addUser=(i,n,p,cp,m)=>{
    this.setState({user:[...this.state.user,{id:i,name:n,password:p,confirmPassword:cp,money:m,payment:[]}]}) // Add user object
  }

  editUser=(id,n,p,cp,m)=>{ // edit user objects
    for(let i = 0; i<this.state.user.length;i++){
      if(this.state.user[i].id === id){
        this.state.user[i].name = n ;
        this.state.user[i].password = p ;
        this.state.user[i].confirmPassword = cp ;
        this.state.user[i].money = m ;
        this.setState({user:[...this.state.user]})
      }
    }
  }

  addProduct = (index,product)=>{ // Add items to user
      this.state.user[index].payment.push(product);
      this.setState({user:[...this.state.user]});        
  }

  balance=(price,index)=>{
    let balance = this.state.user[index].money
    let newBalance = balance - price
    this.state.user[index].money = newBalance
    this.setState({user:[...this.state.user]})
  }

  delUser=(i)=>{ // Delete User
    const delUser = this.state.user.filter((element,index)=>(index!==i))
    this.setState({user : [...delUser]})
  }

  delPro=(i,userIndex)=>{ // Delete Product
    const delPro = this.state.user[userIndex].payment.filter((element,index)=>(index !== i))
    this.state.user[userIndex].payment = delPro
    this.setState({user : [...this.state.user]})
  }

  render() {
    return (
      <div>
        <Router>
          <Switch>
            <Route exact path ='/' component={()=>{return <Homepage user={this.state.user}/>}}/>
            <Route exact path ='/register' component={()=>{return <Register add={this.addUser} user={this.state.user}/>}}/>
            {this.state.user.map((element,index)=>{
              return (
                <Route exact path = {`/edit/${element.name}`} component = {()=>{return <Edit id={element.id} name={element.name} password={element.password} confirmPassword={element.confirmPassword} money={element.money} editUser={this.editUser}/>}}></Route>
              )
            })}
            {this.state.user.map((element,index)=>{
              return (
                <Route exact path = {`/user/${element.name}`} component = {()=>{return <User name={element.name} id={element.id} money={element.money} addProduct={this.addProduct} balance={this.balance} payment={element.payment} index={index}/>}}></Route>
              )
            })}
            {this.state.user.map((element,index)=>{
              return (
                <Route exact path = {`/admin/${element.name}`} component = {()=>{return <Manager name={element.name} id={element.id} money={element.money} delUser={this.delUser} delPro={this.delPro} payment={element.payment} index={index}/>}}></Route>
              )
            })}
          </Switch>
        </Router>
      </div>
    )
  }
}