import React, { Component } from 'react'
import {Link} from 'react-router-dom'

export default class Edit extends Component {
    
    constructor(props) {
        super(props)
    
        this.state = {
            iD:'',
            userName:'',
            userPassword:'',
            confirmPassword:'',
            money:'',
        }
    }
    
    handleUserid=(e)=>{
        this.setState({iD:e.target.value})
    }

    handleName=(e)=>{
        this.setState({userName:e.target.value})
    }

    handlePass=(e)=>{
        this.setState({userPassword:e.target.value})
    }

    confirm=(e)=>{
        this.setState({confirmPassword:e.target.value})
    }

    handlemoney=(e)=>{
        this.setState({money:e.target.value})
    }

    editUser=()=>{
        if((this.state.iD.length === 9) && (this.state.userName.length >= 4) && (this.state.userPassword.length >= 6) && (this.state.userPassword === this.state.confirmPassword) && (this.state.money > 0 && this.state.money <= 1000000) ){
            this.props.editUser(this.state.iD,this.state.userName,this.state.userPassword,this.state.confirmPassword,this.state.money)
        }
    }
    
    render() {
        let vailedId = ''
        let vailedName = ''
        let vailedPassword = ''
        let vailedConfirmPassword = ''
        let vailedMoney = ''
        let submit = <Link to ={`/edit/${this.props.name}`}><button className="btn btn-success" type='button' style={{width:'250px',height:'60px',fontSize:'30px'}} > Edit </button></Link>
        let path = `/user/${this.state.userName}`
        if(this.state.iD.length !== 9 ){
            vailedId = <p style={{color:'red', fontSize:'20px'}}>insert your ID</p> 
        }
        if(this.state.userName.length < 4){ 
            vailedName = <p style={{color:'red', fontSize:'20px'}}>USERNAME must be at least 4 chars"</p>
        }
        if(this.state.userPassword.length < 6){ 
            vailedPassword = <p style={{color:'red', fontSize:'20px'}}>PASSWORD must be at least 6 chars</p> 
        }
        if(this.state.userPassword !== this.state.confirmPassword){
            vailedConfirmPassword = <p style={{color:'red', fontSize:'20px'}}>PASSWORDS must match</p>  
        }
        if(this.state.money < 0 || this.state.money >= 1000000){
            vailedMoney = <p style={{color:'red', fontSize:'20px'}}>MONEY must be between 0-1000000</p>
        }
        if(!vailedId && !vailedName && !vailedPassword && !vailedConfirmPassword && !vailedMoney){
            submit=<Link to ={path}><button onClick={this.editUser} className="btn btn-success" type='button' style={{width:'250px',height:'60px',fontSize:'30px'}} > Edit </button></Link>
        }

        return (
            <div style={{textAlign:'center'}}> 
                <div style={{backgroundColor:'#2093c3'}}>
                     <p style={{fontSize:'60px',fontFamily:'Times new roman',color:'white'}}> SV-BANK Edit </p>
                </div>
                <br/>
                <input type="number" onChange={this.handleUserid} placeholder='ID' style={{width:'300px',height:'45px',fontSize:'30px'}}/>
                <br/><br/>
                {vailedId}
                <input type="text" onChange={this.handleName} placeholder='User Name' style={{width:'300px',height:'45px',fontSize:'30px'}}/>
                <br/><br/>
                {vailedName}
                <input type="password" onChange={this.handlePass} placeholder='Password' style={{width:'300px',height:'45px',fontSize:'30px'}}/>
                <br/><br/>
                {vailedPassword}
                <input type="password" onChange={this.confirm} placeholder='Confirm Password' style={{width:'300px',height:'45px',fontSize:'30px'}}/>
                <br/><br/>
                {vailedConfirmPassword}
                <input type="number" onChange={this.handlemoney} placeholder='Money' style={{width:'300px',height:'45px',fontSize:'30px'}}/>
                {vailedMoney}
                <br/><br/>
                {submit}
                <br/>
                <br/>
                <Link to={`/user/${this.props.name}`}> <button type='button' className='btn btn-primary' style={{fontSize:'30px'}}> Back </button> </Link>
            </div>
        )
    }
}