import React, { Component } from 'react'
import {Link} from 'react-router-dom'

export default class Manager extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             flag:false,
             index:props.index
        }
    }

    change = ()=>{
        this.setState({flag:!this.state.flag})
    }

    show = ()=>{
        if(this.state.flag === true){
            return (
            <div>
                {this.props.payment.map((e,index)=>{
                    return (
                        <div>
                            <p style={{fontSize:'50px',fontFamily:'Times new roman',color:'navy'}} > {e.type} : {e.price} {index.name} </p>
                            <button type='button' className='btn btn-danger' onClick={()=>{this.props.delPro(index,this.state.index)}} style={{width:'150px',height:'60px',fontSize:'30px',position:'relative',left:'40px'}} > X </button> 
                        </div>
                    )
                })}
                <br/>
                <br/>      
            </div>
            )
        }else{
            return (
                <div>
                    <button type='button' className='btn btn-info' onClick={this.change} style={{width:'250px',height:'60px',fontSize:'30px',position:'relative',left:'40px'}} > ||| </button> 
                    <Link to = {`/user/${this.props.name}`}><button type='button' className='btn btn-danger' style={{width:'250px',height:'60px',fontSize:'30px',position:'relative',left:'60px'}}> Back </button></Link>
                    <br/>
                    <br/>
                    <Link to = '/'><button onClick={()=>{this.props.delUser(this.state.index)}} type='button' className='btn btn-danger' style={{width:'250px',height:'60px',fontSize:'30px',position:'relative',left:'60px'}}> Remove User </button></Link>
                </div>
            )
        }
    }

    render() {
        return (
            <div style={{textAlign:'center'}}>
                <br/>   
                <div style={{backgroundColor:'#2093c3'}}>
                     <p style={{fontSize:'60px',fontFamily:'Times new roman',color:'white'}}> SV-BANK Manager </p>
                </div>
                <br/>
                <p style={{fontSize:'50px',fontFamily:'Times new roman',color:'navy'}} > UserID : {this.props.id} , Full Name : {this.props.name} </p>
                {this.show()}
            </div>
        )
    }
}
