import React, { Component } from 'react'
import {Link} from 'react-router-dom'

export default class User extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             flag:false,
             type:'',
             price:'',
             money:props.money,
             index:props.index
        }
    }

    writeType = (e)=>{
        this.setState({type:e.target.value});
    }

    writePrice = (e)=>{
        this.setState({price:e.target.value});
    }
    
    newBalance=()=>{
        alert(`Your balance is ${this.state.money}`)
    }

    change = ()=>{
        this.setState({flag:!this.state.flag})
    }
    
    show = ()=>{
        if(this.state.flag === true){
            return (
                <div>
                   <input onChange={this.writeType} type='text' placeholder='Name Product' style={{width:'300px',height:'45px',fontSize:'30px',position:'relative',top:'20px',left:'40px'}}/>
                   <br/>
                   <br/>
                   <input onChange={this.writePrice} type='number' placeholder = 'Price' style={{width:'300px',height:'45px',fontSize:'30px',position:'relative',top:'20px',left:'40px'}}/>
                   <button type='button' className='btn btn-primary' onClick={()=>{this.props.balance(this.state.price,this.state.index);this.props.addProduct(this.props.index,{price:this.state.price,type:this.state.type})}} style={{fontSize:'30px',position:'relative',left:'60px',top:'10px'}} > Buy </button>
                </div>
            )
        }else{
            return (
            <button type='button' className='btn btn-info' onClick= {this.change} style={{width:'250px',height:'60px',fontSize:'30px'}} > Action </button>
            )
        }
    }
    render() {
        let path = `/admin/${this.props.name}`
        let path1 = `/edit/${this.props.name}`
        return (
        <div>
            <br/>
            <div style={{backgroundColor:'#2093c3',textAlign:'center'}}>
                <p style={{fontSize:'60px',fontFamily:'Times new roman',color:'white'}}> Welcome {this.props.name} </p>
            </div>                
            <br/>
            <br/>
            <div className='container'>
                <div className='row'>
                    <div className ='col-md-3'>
                    </div>
                    <div className ='col-md-3'>
                        <br/>
                        <br/>  
                        <button type='button' className='btn btn-warning'  onClick={this.newBalance} style={{width:'250px',height:'60px',fontSize:'30px'}} >Balance</button>
                    </div>
                    <div className ='col-md-3'>
                        <br/>
                        <br/> 
                        {this.show()}
                    </div>
                </div>
                <div className='row'>
                    <div className ='col-md-3'>
                    </div>
                    <div className ='col-md-3'>
                        <br/>
                        <br/>  
                        <Link to = {path1}><button type='button' className='btn btn-success' style={{width:'250px',height:'60px',fontSize:'30px'}} >Edit</button></Link>
                    </div>
                    <div className ='col-md-3'>
                        <br/>
                        <br/>
                        <Link to = '/'><button type='button' className='btn btn-danger' style={{width:'250px',height:'60px',fontSize:'30px'}}>Exit</button></Link>
                    </div>
                </div>
                <div className='row'>
                    <div className ='col-md-5'>
                    </div>
                    <div className ='col-md-7'>
                        <br/>
                        <br/>
                        <Link to={path}><button className="btn btn-outline-secondary" type="button" style={{width:'250px',height:'60px',fontSize:'30px',position:'relative',right:'50px'}} > ADMIN </button></Link> 
                    </div>
                </div>
            </div>
        </div>
        )
    }
}